export const downloadButtonText = (
  client: string = "",
  platform: string
): string => `Velox Viewer ${client ? client : "GA"} (${platform})`;

export const addPageScroll = (): void => {
  const body = document.querySelector("body");
  if (body) {
    body.style.overflowY = "hidden";
  }
};

export const removePageScroll = (): void => {
  const body = document.querySelector("body");
  if (body) {
    body.style.overflowY = "scroll";
  }
};

export const compareVersions = (
  currentVersion: string,
  version: string
): boolean => {
  if (currentVersion) return currentVersion > version;
  else return false;
};
