import { IVersionsResponse } from './types';
import api from "../api"

export type homePageParams = {
  currver: string | null,
  client: string | null,
}

export const getVersions = (params: homePageParams) => api.get<IVersionsResponse>('/api/versions/',{
  params: {
    currver: params.currver ? params.currver : '',
    client: params.client ? params.client : '',
  }
}).then(({data}) => (data));